import React, {useState} from 'react';
import {useNuiEvent} from "../../hooks/useNuiEvent";
import {fetchNui} from "../../utils/fetchNui";

interface props { 
	currentMenu: string
	togglePicker: any
	eyeColors: any
}

const Face: React.FC<props> = (props) => {

	const [eyeSquint, setEyeSquint] = useState(0)

	const [noseWidth, setNoseWidth] = useState(0)
	const [boneHeight, setBoneHeight] = useState(0)
	const [peakHeight, setPeakHeight] = useState(0)
	const [peakLength, setPeakLength] = useState(0)
	const [peakLowering, setPeakLowering] = useState(0)
	const [boneTwist, setBoneTwist] = useState(0)

	const [eyebrowHeight, setEyebrowHeight] = useState(0)
	const [eyebrowDepth, setEyebrowDepth] = useState(0)

	const [cheecksBoneHeight, setCheecksBoneHeight] = useState(0)
	const [cheecksBoneWidth, setCheecksBoneWidth] = useState(0)
	const [cheecksWidth, setCheecksWidth] = useState(0)

	const [jawBoneWidth, setJawBoneWidth] = useState(0)
	const [jawBoneLength, setJawBoneLength] = useState(0)

	const [chinBoneHeight, setChinBoneHeight] = useState(0)
	const [chinBoneLength, setChinBoneLength] = useState(0)
	const [chinBoneWidth, setChinBoneWidth] = useState(0)
	const [chinCleft, setChinCleft] = useState(0)

	const [lipsThickness, setLipsThickness] = useState(0)
	const [neckThickness, setNeckThickness] = useState(0)

	const rangeChange = (e: any, option: string) => {

		var value = e.target.value

		switch(option) {

			case "Eyes_Openning":
				setEyeSquint(value)
				break
			case "Nose_Width":
				setNoseWidth(value)
				break
			case "Nose_Peak_Hight":
				setPeakLength(value)
				break
			case "Nose_Peak_High":
				setPeakHeight(value)
				break
			case "Nose_Peak_Lenght":
				setPeakHeight(value)
				break
			case "Nose_Peak_Lowering":
				setPeakLowering(value)
				break
			case "Nose_Bone_Twist":
				setBoneTwist(value)
				break
			case "Nose_Bone_High":
				setBoneHeight(value)
				break
			case "EyeBrown_High":
				setEyebrowHeight(value)
				break
			case "EyeBrown_Forward":
				setEyebrowDepth(value)
				break

			case "Cheeks_Bone_High":
				setCheecksBoneHeight(value)
				break
			case "Cheeks_Bone_Width":
				setCheecksBoneWidth(value)
				break
			case "Cheeks_Width":
				setCheecksWidth(value)
				break

			case "Jaw_Bone_Width":
				setJawBoneWidth(value)
				break
			case "Jaw_Bone_Back_Lenght":
				setJawBoneLength(value)
				break

			case "Chdesirerp_Bone_Lowering":
				setChinBoneHeight(value)
				break
			case "Chdesirerp_Bone_Lenght":
				setChinBoneLength(value)
				break
			case "Chdesirerp_Bone_Width":
				setChinBoneWidth(value)
				break
			case "Chdesirerp_Hole":
				setChinCleft(value)
				break

			case "Lips_Thickness":
				setLipsThickness(value)
				break
			case "Neck_Thikness":
				setNeckThickness(value)
				break

		}

		if (value && option) {
			fetchNui('savefacefeatures', {
				name: option,
				scale: value,
			})
		}
			

	}

	useNuiEvent('barberData', (e: any) => {		

		const headStructure = e.headStructure
		for (var x in headStructure) {

			const Value = headStructure[x]

			switch(x) {

				case "Eyes_Openning":
					setEyeSquint(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Width":
					setNoseWidth(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Peak_Hight":
					setPeakLength(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Peak_High":
					setPeakHeight(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Peak_Lenght":
					setPeakHeight(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Peak_Lowering":
					setPeakLowering(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Bone_Twist":
					setBoneTwist(Value === 255 ? 0 : Value * 100)
					break
				case "Nose_Bone_High":
					setBoneHeight(Value === 255 ? 0 : Value * 100)
					break
	
				case "EyeBrown_High":
					setEyebrowHeight(Value === 255 ? 0 : Value * 100)
					break
				case "EyeBrown_Forward":
					setEyebrowDepth(Value === 255 ? 0 : Value * 100)
					break
	
				case "Cheeks_Bone_High":
					setCheecksBoneHeight(Value === 255 ? 0 : Value * 100)
					break
				case "Cheeks_Bone_Width":
					setCheecksBoneWidth(Value === 255 ? 0 : Value * 100)
					break
				case "Cheeks_Width":
					setCheecksWidth(Value === 255 ? 0 : Value * 100)
					break
	
				case "Jaw_Bone_Width":
					setJawBoneWidth(Value === 255 ? 0 : Value * 100)
					break
				case "Jaw_Bone_Back_Lenght":
					setJawBoneLength(Value === 255 ? 0 : Value * 100)
					break
	
				case "Chdesirerp_Bone_Lowering":
					setChinBoneHeight(Value === 255 ? 0 : Value * 100)
					break
				case "Chdesirerp_Bone_Lenght":
					setChinBoneLength(Value === 255 ? 0 : Value * 100)
					break
				case "Chdesirerp_Bone_Width":
					setChinBoneWidth(Value === 255 ? 0 : Value * 100)
					break
				case "Chdesirerp_Hole":
					setChinCleft(Value === 255 ? 0 : Value * 100)
					break
	
				case "Lips_Thickness":
					setLipsThickness(Value === 255 ? 0 : Value * 100)
					break
				case "Neck_Thikness":
					setNeckThickness(Value === 255 ? 0 : Value * 100)
					break
			}

		}

	})

	const GetColor = (colorType: string) => {
		switch(colorType) { 
			case "eye":
				const eyeItem = props.eyeColors.filter(obj => { return obj.active })
				if (eyeItem && eyeItem[0]) { return eyeItem[0]['hex'] }
				return "#ffffff"
		}
	}
		
	return (


			<section className="menuOptions" style={{ display: props.currentMenu === "face" ? '' : 'none' }} >
				<div className="parentOption">
					<span>Eyes</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="leftRight" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>EYE COLOR</div>
								<svg onClick={() => {props.togglePicker(300, 341, "Eye Color")}} className="hexagon" style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M20 0L39.0526 11V33L20 44L0.947441 33V11L20 0Z" fill={GetColor("eye")}/>
									<path d="M1.94744 11.5773L20 1.1547L38.0526 11.5773V32.4226L20 42.8453L1.94744 32.4226V11.5773Z" stroke={ "url(#paint0_linear_241_2587)" } strokeOpacity={ "1" } strokeWidth={ "4" } />				
									<defs>
										<linearGradient id="paint0_linear_241_2587" x1="32" y1="12" x2="32" y2="56" gradientUnits="userSpaceOnUse">
											<stop stopColor="#56FCFC"/>
											<stop offset="1" stopColor="#4E66A8"/>
										</linearGradient>
									</defs>
								</svg>
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Eye Squint</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { rangeChange(e, "Eyes_Openning") }} value={eyeSquint} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Nose</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Nose Width</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Width") }} value={noseWidth} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Bone Height</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Bone_High") }} value={boneHeight} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Peak Height</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Peak_Lenght") }} value={peakHeight} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Peak Length</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Peak_Hight") }} value={peakLength} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Peak Lowering</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Peak_Lowering") }} value={peakLowering} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Bone Twist</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Nose_Bone_Twist") }} value={boneTwist} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Eyebrow</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Eyebrow Height</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "EyeBrown_High") }} value={eyebrowHeight} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Eyebrow Depth</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "EyeBrown_Forward") }} value={eyebrowDepth} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Cheeks</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Cheeks Bone Height</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Cheeks_Bone_High") }} value={cheecksBoneHeight} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Cheeks Bone Width</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Cheeks_Bone_Width") }} value={cheecksBoneWidth} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Cheeks Width</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Cheeks_Width") }} value={cheecksWidth} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Jaw</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Jaw Bone Width</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Jaw_Bone_Width") }} value={jawBoneWidth} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Jaw Bone Length</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Jaw_Bone_Back_Lenght") }} value={jawBoneLength} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>
				
				<div className="parentOption">
					<span>Chin</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Chin Bone Height</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Chdesirerp_Bone_Lowering") }} value={chinBoneHeight} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Chin Bone Length</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Chdesirerp_Bone_Lenght") }} value={chinBoneLength} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Chin Bone Width</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Chdesirerp_Bone_Width") }} value={chinBoneWidth} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Chin Cleft</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Chdesirerp_Hole") }} value={chinCleft} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Other Features</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Lips Thickness</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Lips_Thickness") }} value={lipsThickness} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">Neck Thickness</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="-100" max="100" onChange={(e) => { rangeChange(e, "Neck_Thikness") }} value={neckThickness} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>
				
			</section>
	
	);
}
  
export default Face;