import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-solid-svg-icons'
import {useNuiEvent} from "../../hooks/useNuiEvent";
import {fetchNui} from "../../utils/fetchNui";

interface props { 
	currentMenu: string
	togglePicker: any
	hairColor1: any
	hairColor2: any
	facialHairColor: any
	eyebrowColor: any
	chestHairColor: any
}

const Hair: React.FC<props> = (props) => {

	const [hair, setHair] = useState(0)
	const [hairMax, setHairMax] = useState(0)

	const [hairTexture, setHairTexture] = useState(0)
	const [hairTextureMax, setHairTextureMax] = useState(0)

	const [facialHair, setFacialHair] = useState(0)
	const [facialHairO, setFacialHairO] = useState(100)
	const [facialHairMax, setFacialHairMax] = useState(0)

	const [chestHair, setChestHair] = useState(0)
	const [chestHairO, setChestHairO] = useState(100)
	const [chestHairMax, setChestHairMax] = useState(0)

	const [eyebrows, setEyebrows] = useState(0)
	const [eyebrowsO, setEyebrowsO] = useState(100)
	const [eyebrowsMax, setEyebrowsMax] = useState(0)

	const opacityChange = (e: any, option: string) => {

		var value
		var opacity = e.target.value

		switch(option) {
			case "FacialHair":
				value = facialHair
				setFacialHairO(opacity)
				break
			case "ChestHair":
				value = chestHair
				setChestHairO(opacity)
				break
			case "Eyebrows":
				value = eyebrows
				setEyebrowsO(opacity)
				break
		}

		if (value && opacity) {
			fetchNui('saveheadoverlay', {
				name: option,
				value: value,
				opacity: opacity
			})
		}
			

	}

	const leftRightChange = (incrementDecrease: number, option: string) => {

		var value
		var opacity

		switch(option) {
			case "FacialHair":

				value = facialHair
				opacity = facialHairO

				value = value + incrementDecrease;

				if (value > facialHairMax) { value = 0
				} else if (value < -1) { value = facialHairMax }
				
				setFacialHair(value)

				break

			case "ChestHair":

				value = chestHair
				opacity = chestHairO

				value = value + incrementDecrease;

				if (value > chestHairMax) { value = 0
				} else if (value < -1) { value = chestHairMax }
				
				setChestHair(value)

				break

			case "Eyebrows": 

				value = eyebrows
				opacity = eyebrowsO

				value = value + incrementDecrease;

				if (value > eyebrowsMax) { value = 0
				} else if (value < -1) { value = eyebrowsMax }
				
				setEyebrows(value)

				break
		
			case "Hair": 

				value = hair
				value = value + incrementDecrease;

				if (value > hairMax) { value = 0
				} else if (value < -1) { value = hairMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: hairTexture
				}).then(retData => {
					setHairTexture(0)
					setHairTextureMax(Number(retData))
				})
				
				setHair(value)

				return
			
			case "HairTexture": 

				value = hairTexture
				value = value + incrementDecrease;

				if (value > hairTextureMax) { value = 0
				} else if (value < -1) { value = hairTextureMax }

				fetchNui('updateclothes', {
					name: "hair",
					value: hair,
					texture: value
				})

				setHairTexture(value)

				return

			////////////
		}

		if (value && opacity && option !== 'hair') {
			fetchNui('saveheadoverlay', {
				name: option,
				value: value,
				opacity: opacity
			})
		}

	}

	useNuiEvent('totalData', (e: any) => {

		const drawTotal = e.drawTotal
    
		for (var drawTotalItem in drawTotal) { // Clothing Menu
		  const type = drawTotal[drawTotalItem][0]
		  const max = drawTotal[drawTotalItem][1]
	
		  switch(type) {
			case "hair": setHairMax(Number(max))
		  }
		}	

		const headoverlayTotal = e.headoverlayTotal
		for (var i in headoverlayTotal) {
			const type = i
			const value = headoverlayTotal[i]
			
		  	switch(type) {
				case "FacialHair": 
					setFacialHairMax(Number(value))
					break
				case "ChestHair": 
					setChestHairMax(Number(value))
					break
				case "Eyebrows": 
					setEyebrowsMax(Number(value))
					break
		  	}
		
		}

		const textureTotal = e.textureTotal

		for (var b in textureTotal) {
			switch(b) {
				case "hair": 
					setHairTextureMax(Number(textureTotal[b]))
					break
			}

		}

	})

	useNuiEvent('clothingData', (e: any) => {

		const drawables = e.drawables
		for (var drawItem in drawables) {
			const type = drawables[drawItem][0]
			const currentvalue = drawables[drawItem][1]

			switch(type) {
				case "hair": 
					setHair(Number(currentvalue))
					break
		  	}
			
		}

		const drawtextures = e.drawtextures
		for (var drawtexturesItem in drawtextures) {
			const type = drawtextures[drawtexturesItem][0]
			const currentvalue = drawtextures[drawtexturesItem][1]

			switch(type) {
				case "hair": 
					setHairTexture(currentvalue)
					break
			}
		}
	})

	useNuiEvent('barberData', (e: any) => {		

		const headOverlay = e.headOverlay
		for (var i in headOverlay) {
			const Name = headOverlay[i]['name']
			const Value = headOverlay[i]['overlayValue']
			const Opacity = headOverlay[i]['overlayOpacity']

			switch(Name) {
				case "FacialHair": 
					setFacialHair(Value === 255 ? 0 : Value)
					setFacialHairO(Opacity * 100)
					break
				case "ChestHair": 
					setChestHair(Value === 255 ? 0 : Value)
					setChestHairO(Opacity * 100)
					break
				case "Eyebrows": 
					setEyebrows(Value === 255 ? 0 : Value)
					setEyebrowsO(Opacity * 100)
					break
			}

		}

	})

	const GetColor = (colorType: string) => {
		switch(colorType) { 
			case "hairColor1":
				const hairColor1Item = props.hairColor1.filter(obj => { return obj.active })
				if (hairColor1Item && hairColor1Item[0]) { return hairColor1Item[0]['hex'] }
				return "#ffffff"
			case "hairColor2":
				const hairColor2Item = props.hairColor2.filter(obj => { return obj.active })
				if (hairColor2Item && hairColor2Item[0]) { return hairColor2Item[0]['hex'] }
				return "#ffffff"
			case "facialHairColor":
				const facialHairColorItem = props.facialHairColor.filter(obj => { return obj.active })
				if (facialHairColorItem && facialHairColorItem[0]) { return facialHairColorItem[0]['hex'] }
				return "#ffffff"
			case "chestHairColor":
				const chestHairColorItem = props.chestHairColor.filter(obj => { return obj.active })
				if (chestHairColorItem && chestHairColorItem[0]) { return chestHairColorItem[0]['hex'] }
				return "#ffffff"
			case "eyebrowHairColor":
				const eyebrowsHairColorItem = props.eyebrowColor.filter(obj => { return obj.active })
				if (eyebrowsHairColorItem && eyebrowsHairColorItem[0]) { return eyebrowsHairColorItem[0]['hex'] }
				return "#ffffff"
		}
	}
		
	return (

			<section className="menuOptions" style={{ display: props.currentMenu === "hair" ? '' : 'none' }} >
				
				<div className="parentOption">
					<span>Hair</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">HAIR STYLE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "Hair") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={hair} onChange={ (e: any) => {  leftRightChange(e.target.value - hair , "Hair")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "Hair") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">TEXTURE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "HairTexture") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={hairTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - hairTexture , "HairTexture")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "HairTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>


						<div className="optionDiv">
							<div className="leftRight" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>HAIR COLOR #1</div>
								<svg onClick={() => {props.togglePicker(395, 336, "Hair Color #1")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("hairColor1")} stroke="url(#paint0_linear_241_2814)" strokeWidth="4"/>
										<defs>
											<linearGradient id="paint0_linear_241_2814" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
												<stop stopColor="#56FCFC"/>
												<stop offset="1" stopColor="#4E66A8"/>
											</linearGradient>
										</defs>
								</svg>
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>HAIR COLOR #2</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Hair Color #2")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("hairColor2")} stroke="url(#paint0_linear_241_2814)" strokeWidth="4"/>
										<defs>
											<linearGradient id="paint0_linear_241_2814" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
												<stop stopColor="#56FCFC"/>
												<stop offset="1" stopColor="#4E66A8"/>
											</linearGradient>
										</defs>
								</svg>

							</div>
						</div>

					</div>
				</div>
				
				<div className="parentOption">
					<span>Facial Hair</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Hair Style</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "FacialHair") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={facialHair} onChange={ (e: any) => {  leftRightChange(e.target.value - facialHair , "FacialHair")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "FacialHair") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>
						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "FacialHair") }} value={facialHairO} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight long" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>FACIAL HAIR COLOUR</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Facial Hair Color")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("facialHairColor")} stroke="url(#paint0_linear_241_2814)" strokeWidth="4"/>
										<defs>
											<linearGradient id="paint0_linear_241_2814" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
												<stop stopColor="#56FCFC"/>
												<stop offset="1" stopColor="#4E66A8"/>
											</linearGradient>
										</defs>
								</svg>

							</div>
						</div>


					</div>
				</div>

				<div className="parentOption">
					<span>Chest Hair</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Hair Style</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ChestHair") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={chestHair} onChange={ (e: any) => {  leftRightChange(e.target.value - chestHair , "ChestHair")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "ChestHair") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>
						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "ChestHair") }} value={chestHairO} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight long" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>CHEST HAIR COLOUR</div>

								<svg onClick={() => {props.togglePicker(600, 350, "Chest Hair Color")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("chestHairColor")} stroke="url(#paint0_linear_241_2814)" strokeWidth="4"/>
										<defs>
											<linearGradient id="paint0_linear_241_2814" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
												<stop stopColor="#56FCFC"/>
												<stop offset="1" stopColor="#4E66A8"/>
											</linearGradient>
										</defs>
								</svg>

							</div>
						</div>


					</div>
				</div>

				<div className="parentOption">
					<span>Eyebrows</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Eyebrows Style</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "Eyebrows") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={eyebrows} onChange={ (e: any) => {  leftRightChange(e.target.value - eyebrows , "Eyebrows")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "Eyebrows") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>
						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "Eyebrows") }} value={eyebrowsO} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight long" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>EYEBROWS HAIR COLOUR</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Eyebrows Hair Color")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("eyebrowHairColor")} stroke="url(#paint0_linear_241_2814)" strokeWidth="4"/>
										<defs>
											<linearGradient id="paint0_linear_241_2814" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
												<stop stopColor="#56FCFC"/>
												<stop offset="1" stopColor="#4E66A8"/>
											</linearGradient>
										</defs>
								</svg>
								
							</div>
						</div>


					</div>
				</div>

			</section>
	);
}
  
export default Hair;