import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-solid-svg-icons'
import {fetchNui} from "../../utils/fetchNui";
import {useNuiEvent} from "../../hooks/useNuiEvent";

interface props { 
	currentMenu: string
	currentParent: string
}

const Clothing: React.FC<props> = (props) => {

	const [hair, setHair] = useState(0)
	const [hairMax, setHairMax] = useState(0)
	const [hairTexture, setHairTexture] = useState(0)
	const [hairTextureMax, setHairTextureMax] = useState(0)

	const [jackets, setJackets] = useState(0)
	const [jacketsMax, setJacketsMax] = useState(0)
	const [jacketsTexture, setJacketsTexture] = useState(0)
	const [jacketsTextureMax, setJacketsTextureMax] = useState(0)

	const [shirts, setShirts] = useState(0)
	const [shirtsMax, setShirtsMax] = useState(0)
	const [shirtsTexture, setShirtsTexture] = useState(0)
	const [shirtsTextureMax, setShirtsTextureMax] = useState(0)

	const [torsos, setTorsos] = useState(0) // ARMS / GLOVES
	const [torsosMax, setTorsosMax] = useState(0)
	const [torsosTexture, setTorsosTexture] = useState(0)
	const [torsosTextureMax, setTorsosTextureMax] = useState(0)

	const [legs, setLegs] = useState(0) // PANTS
	const [legsMax, setLegsMax] = useState(0)
	const [legsTexture, setLegsTexture] = useState(0)
	const [legsTextureMax, setLegsTextureMax] = useState(0)

	const [shoes, setShoes] = useState(0)
	const [shoesMax, setShoesMax] = useState(0)
	const [shoesTexture, setShoesTexture] = useState(0)
	const [shoesTextureMax, setShoesTextureMax] = useState(0)

	const [decals, setDecals] = useState(0)
	const [decalsMax, setDecalsMax] = useState(0)
	const [decalsTexture, setDecalsTexture] = useState(0)
	const [decalsTextureMax, setDecalsTextureMax] = useState(0)

	const [hats, setHats] = useState(0)
	const [hatsMax, setHatsMax] = useState(0)
	const [hatsTexture, setHatsTexture] = useState(0)
	const [hatsTextureMax, setHatsTextureMax] = useState(0)

	useNuiEvent('totalData', (e: any) => {

		const drawTotal = e.drawTotal
    
		for (var drawTotalItem in drawTotal) { // Clothing Menu
		  const type = drawTotal[drawTotalItem][0]
		  const max = drawTotal[drawTotalItem][1]
	
		  switch(type) {
			case "hair": 
				setHairMax(Number(max))
				break
			case "jackets":
				setJacketsMax(Number(max))
				break
			case "shirts":
				setShirtsMax(Number(max))
				break
			case "torsos":
				setTorsosMax(Number(max))
				break
			case "legs":
				setLegsMax(Number(max))
				break
			case "shoes":
				setShoesMax(Number(max))
				break
			case "decals":
				setDecalsMax(Number(max))
				break
		  }
		}	

		const propDrawTotal = e.propDrawTotal 
		for (var item in propDrawTotal) { // Clothing Menu
			const type = propDrawTotal[item][0]
			const max = propDrawTotal[item][1]

			switch(type) {
				case "hats": setHatsMax(Number(max))
			}
		}

		const textureTotal = e.textureTotal

		for (var i in textureTotal) {
			const type = i
			const max = textureTotal[i]

			switch(type) {
				case "hair": 
					setHairTextureMax(Number(max))
					break
				case "hats": 
					setHatsTextureMax(Number(max))
					break
				case "jackets": 
					setJacketsTextureMax(Number(max))
					break
				case "shirts": 
					setShirtsTextureMax(Number(max))
					break
				case "torsos": 
					setTorsosTextureMax(Number(max))
					break
				case "legs": 
					setLegsTextureMax(Number(max))
					break
				case "shoes": 
					setShoesTextureMax(Number(max))
					break
				case "decals": 
					setDecalsTextureMax(Number(max))
					break
			}

		}

	})

	useNuiEvent('clothingData', (e: any) => {
		const drawables = e.drawables
		for (var drawItem in drawables) {
			const type = drawables[drawItem][0]
			const currentvalue = drawables[drawItem][1]

			switch(type) {
				case "hair": 
					setHair(Number(currentvalue))
					break
				case "jackets": 
					setJackets(Number(currentvalue))
					break
				case "shirts": 
					setShirts(Number(currentvalue))
					break
				case "torsos": 
					setTorsos(Number(currentvalue))
					break
				case "legs": 
					setLegs(Number(currentvalue))
					break
				case "shoes": 
					setShoes(Number(currentvalue))
					break
				case "decals": 
					setDecals(Number(currentvalue))
					break
		  	}
			
		}
		
		const props = e.props
		for (var item in props) {
			const type = props[item][0]
			const currentvalue = props[item][1]

			switch(type) {
				case "hats": 
				setHats(Number(currentvalue))
				break
			}

		}

		const drawtextures = e.drawtextures
			for (var drawtexturesItem in drawtextures) {
				const type = drawtextures[drawtexturesItem][0]
				const currentvalue = drawtextures[drawtexturesItem][1]

				switch(type) {
					case "hair": 
						setHairTexture(Number(currentvalue))
						break
					case "jackets": 
						setJacketsTexture(Number(currentvalue))
						break
					case "shirts": 
						setShirtsTexture(Number(currentvalue))
						break
					case "torsos": 
						setTorsosTexture(Number(currentvalue))
						break
					case "legs": 
						setLegsTexture(Number(currentvalue))
						break
					case "shoes": 
						setShoesTexture(Number(currentvalue))
						break
					case "decals": 
						setDecalsTexture(Number(currentvalue))
						break
				}
		}

	})

	const leftRightChange = (incrementDecrease: number, option: string) => {

		let value;

		switch(option) {

			case "hair":

				value = hair
				value = value + incrementDecrease;

				if (value > hairMax) { value = 0
				} else if (value < -1) { value = hairMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setHair(value)
					setHairTexture(0)
					setHairTextureMax(Number(retData))
				})

				break

			case "hats":

				value = hats
				value = value + incrementDecrease;

				if (value > hatsMax) { value = 0
				} else if (value < -1) { value = hatsMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setHats(value)
					setHatsTexture(0)
					setHatsTextureMax(Number(retData))
				})

				break

			case "jackets":

				value = jackets
				value = value + incrementDecrease;

				if (value > jacketsMax) { value = 0
				} else if (value < -1) { value = jacketsMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setJackets(value)
					setJacketsTexture(0)
					setJacketsTextureMax(Number(retData))
				})

				break

			case "shirts":

				value = shirts
				value = value + incrementDecrease;

				if (value > shirtsMax) { value = 0
				} else if (value < -1) { value = shirtsMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setShirts(value)
					setShirtsTexture(0)
					setShirtsTextureMax(Number(retData))
				})

				break
			
			case "torsos":

				value = torsos
				value = value + incrementDecrease;

				if (value > torsosMax) { value = 0
				} else if (value < -1) { value = torsosMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setTorsos(value)
					setTorsosTexture(0)
					setTorsosTextureMax(Number(retData))
				})

				break

			case "legs":

				value = legs
				value = value + incrementDecrease;

				if (value > legsMax) { value = 0
				} else if (value < -1) { value = legsMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setLegs(value)
					setLegsTexture(0)
					setLegsTextureMax(Number(retData))
				})

				break

			case "shoes":

				value = shoes
				value = value + incrementDecrease;

				if (value > shoesMax) { value = 0
				} else if (value < -1) { value = shoesMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setShoes(value)
					setShoesTexture(0)
					setShoesTextureMax(Number(retData))
				})

				break
				
			case "decals":

				value = decals
				value = value + incrementDecrease;

				if (value > decalsMax) { value = 0
				} else if (value < -1) { value = decalsMax }

				fetchNui('updateclothes', {
					name: option.toLowerCase(),
					value: value,
					texture: 0
				}).then(retData => {
					setDecals(value)
					setDecalsTexture(0)
					setDecalsTextureMax(Number(retData))
				})

				break

			case "hairTexture": 

				value = hairTexture
				value = value + incrementDecrease;

				if (value > hairTextureMax) { value = 0
				} else if (value < -1) { value = hairTextureMax }

				fetchNui('updateclothes', {
					name: "hair",
					value: hair,
					texture: value
				})

				setHairTexture(value)

				break

			case "hatsTexture": 

				value = hatsTexture
				value = value + incrementDecrease;

				if (value > hatsTextureMax) { value = 0
				} else if (value < -1) { value = hatsTextureMax }

				fetchNui('updateclothes', {
					name: "hats",
					value: hats,
					texture: value
				})

				setHatsTexture(value)

				break

			case "jacketsTexture": 

				value = jacketsTexture
				value = value + incrementDecrease;

				if (value > jacketsTextureMax) { value = 0
				} else if (value < -1) { value = jacketsTextureMax }

				fetchNui('updateclothes', {
					name: "jackets",
					value: jackets,
					texture: value
				})

				setJacketsTexture(value)

				break

			case "shirtsTexture": 

				value = shirtsTexture
				value = value + incrementDecrease;

				if (value > shirtsTextureMax) { value = 0
				} else if (value < -1) { value = shirtsTextureMax }

				fetchNui('updateclothes', {
					name: "shirts",
					value: shirts,
					texture: value
				})

				setShirtsTexture(value)
				
				break

			case "torsosTexture": 

				value = torsosTexture
				value = value + incrementDecrease;

				if (value > torsosTextureMax) { value = 0
				} else if (value < -1) { value = torsosTextureMax }

				fetchNui('updateclothes', {
					name: "torsos",
					value: torsos,
					texture: value
				})

				setTorsosTexture(value)
				
				break

			case "legsTexture": 

				value = legsTexture
				value = value + incrementDecrease;

				if (value > legsTextureMax) { value = 0
				} else if (value < -1) { value = legsTextureMax }

				fetchNui('updateclothes', {
					name: "legs",
					value: legs,
					texture: value
				})

				setLegsTexture(value)
				
				break
				
			case "shoesTexture": 

				value = shoesTexture
				value = value + incrementDecrease;

				if (value > shoesTextureMax) { value = 0
				} else if (value < -1) { value = shoesTextureMax }

				fetchNui('updateclothes', {
					name: "shoes",
					value: shoes,
					texture: value
				})

				setShoesTexture(value)
				
				break

			case "decalsTexture": 

				value = decalsTexture
				value = value + incrementDecrease;

				if (value > decalsTextureMax) { value = 0
				} else if (value < -1) { value = decalsTextureMax }

				fetchNui('updateclothes', {
					name: "decals",
					value: decals,
					texture: value
				})

				setDecalsTexture(value)
				
				break

		}

	}
	
	return (
		<section className="menuOptions" style={{ display: props.currentMenu === "clothing" && props.currentParent === 'clothing' ? '' : 'none' }} >

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">HAIR STYLE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "hair") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={hair} onChange={ (e: any) => {  leftRightChange(e.target.value - hair , "hair")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "hair") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "hairTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={hairTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - hairTexture , "hairTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "hairTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>
			
			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">HATS</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "hats") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={hats} onChange={ (e: any) => {  leftRightChange(e.target.value - hats , "hats")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "hats") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "hatsTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={hatsTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - hatsTexture , "hatsTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "hatsTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">JACKETS</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "jackets") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={jackets} onChange={ (e: any) => {  leftRightChange(e.target.value - jackets , "jackets")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "jackets") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "jacketsTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={jacketsTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - jacketsTexture , "jacketsTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "jacketsTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>
				
			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">SHIRTS</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "shirts") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={shirts} onChange={ (e: any) => {  leftRightChange(e.target.value - shirts , "shirts")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "shirts") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "shirtsTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={shirtsTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - shirtsTexture , "shirtsTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "shirtsTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">ARMS/GLOVES</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "torsos") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={torsos} onChange={ (e: any) => {  leftRightChange(e.target.value - torsos , "torsos")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "torsos") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "torsosTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={torsosTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - torsosTexture , "torsosTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "torsosTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">PANTS</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "legs") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={legs} onChange={ (e: any) => {  leftRightChange(e.target.value - legs , "legs")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "legs") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "legsTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={legsTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - legsTexture , "legsTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "legsTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">SHOES</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "shoes") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={shoes} onChange={ (e: any) => {  leftRightChange(e.target.value - shoes , "shoes")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "shoes") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "shoesTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={shoesTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - shoesTexture , "shoesTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "shoesTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">DECALS</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "decals") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={decals} onChange={ (e: any) => {  leftRightChange(e.target.value - decals , "decals")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "decals") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "decalsTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={decalsTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - decalsTexture , "decalsTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "decalsTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			</section>
	
	);
}
  
export default Clothing;