import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight, faMinus } from '@fortawesome/pro-solid-svg-icons'
import {fetchNui} from "../../utils/fetchNui";
import {useNuiEvent} from "../../hooks/useNuiEvent";

interface props { 
	currentMenu: string
	currentParent: string
}

const Tattoo: React.FC<props> = (props) => {

	const [torsoTats, setTorsoTats] = useState([])
	const [headTats, setHeadTats] = useState([])
	const [leftArmTats, setLeftArmTats] = useState([])
	const [rightArmTats, setRightArmTats] = useState([])
	const [leftLegTats, setLeftLegTats] = useState([])
	const [rightLegTats, setRightLegTats] = useState([])

	const [headMax, setHeadMax] = useState(0)
	const [leftArmMax, setLeftArmMax] = useState(0)
	const [rightArmMax, setRightArmMax] = useState(0)
	const [leftLegMax, setLeftLegMax] = useState(0)
	const [rightLegMax, setRightLegMax] = useState(0)
	const [torsoMax, setTorsoMax] = useState(0)

	const leftRightChange = (incrementDecrease: number, option: string, filterKey: number, valKey: number) => {

		let currentTats
		var oldValue
		var Value
		var newValue
		
		switch(option) {
			case "ZONE_HEAD":

				currentTats = [...headTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > headMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = headMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setHeadTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})

				break
			case "ZONE_LEFT_ARM":
				
				currentTats = [...leftArmTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > leftArmMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = leftArmMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setLeftArmTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})
				break
			case "ZONE_RIGHT_ARM":
				
				currentTats = [...rightArmTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > rightArmMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = rightArmMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setRightArmTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})
				break
			case "ZONE_RIGHT_LEG":
				
				currentTats = [...rightLegTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > rightLegMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = rightLegMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setRightLegTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})
				break
			case "ZONE_LEFT_LEG":
				
				currentTats = [...leftLegTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > leftLegMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = leftLegMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setLeftLegTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})
				break
			case "ZONE_TORSO":
				
				currentTats = [...torsoTats]

				oldValue = currentTats[filterKey]
				Value = 0

				if (oldValue) Value = oldValue.number

				newValue = Value + incrementDecrease

				if (newValue > torsoMax) {
					newValue = 1;
				} else if (newValue <= 0) {
					newValue = torsoMax;
				}

				currentTats[filterKey] = { number: newValue, key: valKey } 
				setTorsoTats(currentTats)
				
				fetchNui('edittattoo', {
					name: option,
					key: valKey,
					oldValue: oldValue,
					newValue: newValue
				})
				break
		}

	}

	useNuiEvent('tattooData', (e: any) => {

		for (var item in e.tats) {

			switch(item) {
				case "ZONE_HEAD":
					setHeadTats(e.tats[item])
					break
				case "ZONE_LEFT_ARM":
					setLeftArmTats(e.tats[item])
					break
				case "ZONE_RIGHT_ARM":
					setRightArmTats(e.tats[item])
					break
				case "ZONE_RIGHT_LEG":
					setRightLegTats(e.tats[item])
					break
				case "ZONE_LEFT_LEG":
					setLeftLegTats(e.tats[item])
					break
				case "ZONE_TORSO":
					setTorsoTats(e.tats[item])
					break
			}
		}

		for (var categItem in e.tatCategory) {
			const type = e.tatCategory[categItem][0]
			const value = e.tatCategory[categItem][1]		
			
			switch(type) {
				case "ZONE_HEAD":
					setHeadMax(Number(value))
					break
				case "ZONE_LEFT_ARM":
					setLeftArmMax(Number(value))
					break
				case "ZONE_RIGHT_ARM":
					setRightArmMax(Number(value))
					break
				case "ZONE_RIGHT_LEG":
					setRightLegMax(Number(value))
					break
				case "ZONE_LEFT_LEG":
					setLeftLegMax(Number(value))
					break
				case "ZONE_TORSO":
					setTorsoMax(Number(value))
					break
			}

		}

	})

	const remoteTat = (option: string, valKey: number) => {

		let filteredArray;

		switch(option) {
			case "ZONE_HEAD":

				filteredArray = headTats.filter((item) => { return item.key !== valKey  })
				setHeadTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })
				
				break
			case "ZONE_LEFT_ARM":
				
				filteredArray = leftArmTats.filter((item) => { return item.key !== valKey  })
				setLeftArmTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })

				break
			case "ZONE_RIGHT_ARM":
				
				filteredArray = rightArmTats.filter((item) => { return item.key !== valKey  })
				setRightArmTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })

				break
			case "ZONE_RIGHT_LEG":
				
				filteredArray = rightLegTats.filter((item) => { return item.key !== valKey  })
				setRightLegTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })

				break
			case "ZONE_LEFT_LEG":
				
				filteredArray = leftLegTats.filter((item) => { return item.key !== valKey  })
				setLeftLegTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })

				break
			case "ZONE_TORSO":
				
				filteredArray = torsoTats.filter((item) => { return item.key !== valKey  })
				setTorsoTats(filteredArray)
				fetchNui('removetattoo', { key: valKey })

				break
		}
		
	}

	const addTat = (option: string) => {

		switch(option) {
			case "ZONE_HEAD":

				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...headTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setHeadTats(currentTats)
				})
				
				break
			case "ZONE_LEFT_ARM":
				
				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...leftArmTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setLeftArmTats(currentTats)
				})

				break
			case "ZONE_RIGHT_ARM":
				
				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...rightArmTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setRightArmTats(currentTats)
				})

				break
			case "ZONE_RIGHT_LEG":
				
				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...rightLegTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setRightLegTats(currentTats)
				})

				break
			case "ZONE_LEFT_LEG":
				
				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...leftLegTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setLeftLegTats(currentTats)
				})

				break
			case "ZONE_TORSO":
				
				fetchNui('AddTat', { name: option }).then(retData => {
					let currentTats = [...torsoTats]
					currentTats[currentTats.length] = {
						number: 1,
						key: retData
					}
					setTorsoTats(currentTats)
				})

				break
		}

	}
	
	return (
		<section className="menuOptions" style={{ display: props.currentMenu === "tattoo" && props.currentParent === 'tattoo' ? '' : 'none' }} >

			<div className="optionDiv">
				<div className="optionText">TORSO</div>

				{ torsoTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_TORSO") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_TORSO") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{ torsoTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_TORSO", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_TORSO", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_TORSO", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				) }
				<div className="addTat" onClick={() => { addTat("ZONE_TORSO") }} > ADD </div>

			</div>
			
			<div className="optionDiv">
				<div className="optionText">HEAD</div>

				{ headTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_HEAD") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_HEAD") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{headTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_HEAD", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_HEAD", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_HEAD", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				)}
				<div className="addTat" onClick={() => { addTat("ZONE_HEAD") }} > ADD </div>

			</div>

			<div className="optionDiv">
				<div className="optionText">LEFT ARM</div>

				{ leftArmTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_LEFT_ARM") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_LEFT_ARM") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{leftArmTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_LEFT_ARM", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_LEFT_ARM", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_LEFT_ARM", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				)}
				<div className="addTat" onClick={() => { addTat("ZONE_LEFT_ARM") }} > ADD </div>

			</div>

			<div className="optionDiv">
				<div className="optionText">RIGHT ARM</div>

				{ rightArmTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_RIGHT_ARM") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_RIGHT_ARM") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{rightArmTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_RIGHT_ARM", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_RIGHT_ARM", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_RIGHT_ARM", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				)}
				<div className="addTat" onClick={() => { addTat("ZONE_RIGHT_ARM") }} > ADD </div>

			</div>

			<div className="optionDiv">
				<div className="optionText">LEFT LEG</div>

				{ leftLegTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_LEFT_LEG") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_LEFT_LEG") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{leftLegTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_LEFT_LEG", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_LEFT_LEG", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_LEFT_LEG", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				)}
				<div className="addTat" onClick={() => { addTat("ZONE_LEFT_LEG") }} > ADD </div>

			</div>

			<div className="optionDiv">
				<div className="optionText">RIGHT LEG</div>

				{ rightLegTats.length === 0 ? <div className="flexRow leftRight long">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { addTat("ZONE_RIGHT_LEG") }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">0</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { addTat("ZONE_RIGHT_LEG") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div> : 
					<div>
				</div> }

				{rightLegTats.map((tattoo: any, key: number) =>
					<div key={key} className="flexRow">
						<div className="leftRight medium">
							<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "ZONE_RIGHT_LEG", key, tattoo.key) }} fontSize={20} color={"#56FCFC"} />
							<div style={{ paddingLeft: '76px' }} className="optionNumber">{tattoo.number}</div>
							<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={() => { leftRightChange(1, "ZONE_RIGHT_LEG", key, tattoo.key) }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
						</div>

						<div className="removeTat">
							<FontAwesomeIcon onClick={() => { remoteTat("ZONE_RIGHT_LEG", tattoo.key) }} icon={faMinus} fontSize={20} color={"#56FCFC"} />
						</div>
						
					</div>
				)}
				<div className="addTat" onClick={() => { addTat("ZONE_RIGHT_LEG") }} > ADD </div>

			</div>

		</section>
	
	);
}
  
export default Tattoo;