import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faXmark } from '@fortawesome/pro-solid-svg-icons'
import {fetchNui} from "../../utils/fetchNui";

import {CSSTransition} from 'react-transition-group';

interface props {
	colorpicker: boolean
	togglePicker: any
	colorPickerPosT: number
	colorPickerPosR: number
    colorPickerType: string

    eyeColors: any
    hairColor1: any
    hairColor2: any
    facialHairColor: any
	chestHairColor: any
    eyebrowColor: any
    makeupColor1: any
    makeupColor2: any
    blushColor: any
    lipstickColor: any

    setEyeColors: any
    setHairColor1: any
    setHairColor2: any
    setFacialHairColor: any
	setChestHairColor: any
    setEyebrowColor: any
    setMakeupColor1: any
    setMakeupColor2: any
    setBlushColor: any
    setLipstickColor: any
}

const ColorPicker: React.FC<props> = (props) => {

	const colorClick = (key: number) => {
		switch(props.colorPickerType) {
			case "Eye Color":
				
					let currentColors1 = [...props.eyeColors]
                    currentColors1.forEach((element, index) => { 
						currentColors1[index].active = false; 
					})

					currentColors1[key] = { ...currentColors1[key], active: true }
					props.setEyeColors(currentColors1)
                    fetchNui('saveeyecolor', { firstColour: key })

				break

                        case "Hair Color #1":
				
				let currentColors2 = [...props.hairColor1]
                                currentColors2.forEach((element, index) => { currentColors2[index].active = false; })

				currentColors2[key] = { ...currentColors2[key], active: true }
				props.setHairColor1(currentColors2)
                                fetchNui('savehaircolor', { firstColour: key + 1 })

				break

                        case "Hair Color #2":
				
				let currentColors3 = [...props.hairColor2]
                                currentColors3.forEach((element, index) => { currentColors3[index].active = false; })

				currentColors3[key] = { ...currentColors3[key], active: true }
				props.setHairColor2(currentColors3)
                                fetchNui('savehaircolor', { secondColour: key + 1 })

				break

                        case "Facial Hair Color":
				
				let currentColors4 = [...props.facialHairColor]
                                currentColors4.forEach((element, index) => { currentColors4[index].active = false; })

				currentColors4[key] = { ...currentColors4[key], active: true }
				props.setFacialHairColor(currentColors4)
                                fetchNui('saveheadoverlaycolor', { name: "FacialHair", firstColour: key + 1 })

				break

				case "Chest Hair Color":
				
						let currentColors44 = [...props.chestHairColor]
						currentColors44.forEach((element, index) => { currentColors44[index].active = false; })
	
						currentColors44[key] = { ...currentColors44[key], active: true }
						props.setChestHairColor(currentColors44)
						fetchNui('saveheadoverlaycolor', { name: "ChestHair", firstColour: key + 1 })
	
						break

                case "Eyebrows Hair Color":
				
				let currentColors5 = [...props.eyebrowColor]
                                currentColors5.forEach((element, index) => { currentColors5[index].active = false; })

				currentColors5[key] = { ...currentColors5[key], active: true }
				props.setEyebrowColor(currentColors5)
                                fetchNui('saveheadoverlaycolor', { name: "Eyebrows", firstColour: key + 1 })

				break

                        case "Makeup Color #1":
				
				let currentColors6 = [...props.makeupColor1]
                                currentColors6.forEach((element, index) => { currentColors6[index].active = false; })

				currentColors6[key] = { ...currentColors6[key], active: true }
				props.setMakeupColor1(currentColors6)
                                fetchNui('saveheadoverlaycolor', { name: "Makeup", firstColour: key + 1 })

				break

                        case "Makeup Color #2":
				
				let currentColors7 = [...props.makeupColor1]
                                currentColors7.forEach((element, index) => { currentColors7[index].active = false; })

				currentColors7[key] = { ...currentColors7[key], active: true }
				props.setMakeupColor2(currentColors7)
                                fetchNui('saveheadoverlaycolor', { name: "Makeup", secondColour: key + 1 })

				break

                        case "Blush Color":
				
				let currentColors8 = [...props.blushColor]
                                currentColors8.forEach((element, index) => { currentColors8[index].active = false; })

				currentColors8[key] = { ...currentColors8[key], active: true }
				props.setBlushColor(currentColors8)
                                fetchNui('saveheadoverlaycolor', { name: "Blush", firstColour: key + 1 })

				break

                         case "Lipstick Color":
				
				let currentColors9 = [...props.lipstickColor]
                                currentColors9.forEach((element, index) => { currentColors9[index].active = false; })

				currentColors9[key] = { ...currentColors9[key], active: true }
				props.setLipstickColor(currentColors9)
                                fetchNui('saveheadoverlaycolor', { name: "Lipstick", firstColour: key + 1 })

				break
		}
		
	}

	const Hexagon = (props: any) => {
		return <svg className="hexagon" key={props.number} onClick={() => { colorClick(props.number) }}  style={{ filter: props.active ? "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" : "" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">

				<path d="M20 0L39.0526 11V33L20 44L0.947441 33V11L20 0Z" fill={props.hex}/>
				<path d="M1.94744 11.5773L20 1.1547L38.0526 11.5773V32.4226L20 42.8453L1.94744 32.4226V11.5773Z" stroke={ props.active ? "url(#paint0_linear_241_2587)" : "url(#paint0_linear_241_2582)" } strokeOpacity={ props.active ? "1" : "0.45" } strokeWidth={ props.active ? "4" : "2" } />				

				<defs>

					<linearGradient id="paint0_linear_241_2582" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
						<stop stopColor="white"/>
						<stop offset="1" stopColor="white" stopOpacity="0"/>
					</linearGradient>

					<linearGradient id="paint0_linear_241_2587" x1="32" y1="12" x2="32" y2="56" gradientUnits="userSpaceOnUse">
						<stop stopColor="#56FCFC"/>
						<stop offset="1" stopColor="#4E66A8"/>
					</linearGradient>

				</defs>

			</svg>
		
	}
		
	return (
		<CSSTransition in={props.colorpicker} timeout={200} unmountOnExit classNames="fade">
			<section className="colorPicker" style={{ top: props.colorPickerPosT + 'px',  right: props.colorPickerPosR + 'px' }} >
				<header className="header">
					<span>{props.colorPickerType}</span>
					<FontAwesomeIcon onClick={() => {props.togglePicker(0, 0, "")}} icon={faXmark} fontSize={20} style={{ marginLeft: "auto" }} color={"white"} />
				</header>
				<div className="options">

					{(() => {
						switch(props.colorPickerType) {
							case "Eye Color":
								return( props.eyeColors.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Hair Color #1":
								return( props.hairColor1.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Hair Color #2":
								return( props.hairColor2.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Facial Hair Color":
								return( props.facialHairColor.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
							case "Chest Hair Color":
								return( props.chestHairColor.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Eyebrows Hair Color":
								return( props.eyebrowColor.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Makeup Color #1":
								return( props.makeupColor1.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Makeup Color #2":
								return( props.makeupColor2.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Blush Color":
								return( props.blushColor.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
                                                        case "Lipstick Color":
								return( props.lipstickColor.map((object, key) => <Hexagon hex={object.hex} active={object.active} key={key} number={key} /> ) 
							)
						}
					})()}

				</div>
			</section>
			
		</CSSTransition>
	);
}
  
export default ColorPicker;