import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-solid-svg-icons'
import {useNuiEvent} from "../../hooks/useNuiEvent";
import {fetchNui} from "../../utils/fetchNui";

interface props { 
	currentMenu: string
	togglePicker: any
	makeupColor1: any
	makeupColor2: any
	blushColor: any
	lipstickColor: any
}

const Makeup: React.FC<props> = (props) => {

	const [makeup, setMakeup] = useState(0)
	const [makeupO, setMakeupO] = useState(100)
	const [makeupMax, setMakeupMax] = useState(0)

	const [blush, setBlush] = useState(0)
	const [blushO, setBlushO] = useState(100)
	const [blushMax, setBlushMax] = useState(0)
	
	const [lipstick, setLipstick] = useState(0)
	const [lipstickO, setLipstickO] = useState(100)
	const [lipstickMax, setLipstickMax] = useState(0)

	const opacityChange = (e: any, option: string) => {

		var value
		var opacity = e.target.value

		switch(option) {
			case "Makeup":
				value = makeup
				setMakeupO(opacity)
				break
			case "Blush":
				value = blush
				setBlushO(opacity)
				break
			case "Lipstick":
				value = lipstick
				setLipstickO(opacity)
				break
		}

		if (value && opacity) {
			fetchNui('saveheadoverlay', {
				name: option,
				value: value,
				opacity: opacity
			})
		}
			

	}

	const leftRightChange = (incrementDecrease: number, option: string) => {

		var value
		var opacity

		switch(option) {
			case "Makeup":

				value = makeup
				opacity = makeupO

				value = value + incrementDecrease;

				if (value > makeupMax) { value = 0
				} else if (value < -1) { value = makeupMax }
				
				setMakeup(value)

				break

			case "Blush": 

				value = blush
				opacity = blushO

				value = value + incrementDecrease;

				if (value > blushMax) { value = 0
				} else if (value < -1) { value = blushMax }
				
				setBlush(value)

				break

			case "Lipstick": 

				value = lipstick
				opacity = lipstickO

				value = value + incrementDecrease;

				if (value > lipstickMax) { value = 0
				} else if (value < -1) { value = lipstickMax }
				
				setLipstick(value)

				break

		}

		if (value && opacity) {
			fetchNui('saveheadoverlay', {
				name: option,
				value: value,
				opacity: opacity
			})
		}

	}

	useNuiEvent('totalData', (e: any) => {

		const headoverlayTotal = e.headoverlayTotal
		for (var i in headoverlayTotal) {
			const type = i
			const value = headoverlayTotal[i]
			
		  	switch(type) {
				case "Makeup": 
					setMakeupMax(value)
					break
				case "Blush": 
					setBlushMax(value)
					break
				case "Lipstick": 
					setLipstickMax(value)
					break
		  	}
		
		}

	})

	useNuiEvent('barberData', (e: any) => {		

		const headOverlay = e.headOverlay
		for (var i in headOverlay) {
			const Name = headOverlay[i]['name']
			const Value = headOverlay[i]['overlayValue']
			const Opacity = headOverlay[i]['overlayOpacity']

			switch(Name) {
				case "Makeup": 
					setMakeup(Value === 255 ? 0 : Value)
					setMakeupO(Opacity * 100)
					break
				case "Blush": 
					setBlush(Value === 255 ? 0 : Value)
					setBlushO(Opacity * 100)
					break
				case "Lipstick": 
					setLipstick(Value === 255 ? 0 : Value)
					setLipstickO(Opacity * 100)
					break
			}

		}
		
	})

	const GetColor = (colorType: string) => {
		switch(colorType) { 
			case "makeupColor1":
				const makeup1Item = props.makeupColor1.filter(obj => { return obj.active })
				if (makeup1Item && makeup1Item[0]) { return makeup1Item[0]['hex'] }
				return "#ffffff"
			case "makeupColor2":
				const makeup2Item = props.makeupColor2.filter(obj => { return obj.active })
				if (makeup2Item && makeup2Item[0]) { return makeup2Item[0]['hex'] }
				return "#ffffff"
			case "blushColor":
				const blushItem = props.blushColor.filter(obj => { return obj.active })
				if (blushItem && blushItem[0]) { return blushItem[0]['hex'] }
				return "#ffffff"
			case "lipstickColor":
				const lipstickColorItem = props.lipstickColor.filter(obj => { return obj.active })
				if (lipstickColorItem && lipstickColorItem[0]) { return lipstickColorItem[0]['hex'] }
				return "#ffffff"
		}
	}
		
	return (

			<section className="menuOptions" style={{ display: props.currentMenu === "makeup" ? '' : 'none' }} >
				
				<div className="parentOption">
					<span>Makeup</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">MAKEUP STYLE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "Makeup") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={makeup} onChange={ (e: any) => {  leftRightChange(e.target.value - makeup , "Makeup")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "Makeup") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "Makeup") }} value={makeupO} />
									</div>
									
								</div>
								
							</div>
						</div>


						<div className="optionDiv">
							<div className="leftRight" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>MAKEUP COLOR #1</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Makeup Color #1")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("makeupColor1")} stroke="url(#paint0_linear_241_2883)" strokeWidth="4"/>
									<defs>
									<linearGradient id="paint0_linear_241_2883" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
									<stop stopColor="#56FCFC"/>
									<stop offset="1" stopColor="#4E66A8"/>
									</linearGradient>
									</defs>
								</svg>

							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>MAKEUP COLOR #2</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Makeup Color #2")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("makeupColor2")} stroke="url(#paint0_linear_241_2883)" strokeWidth="4"/>
									<defs>
									<linearGradient id="paint0_linear_241_2883" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
									<stop stopColor="#56FCFC"/>
									<stop offset="1" stopColor="#4E66A8"/>
									</linearGradient>
									</defs>
								</svg>

							</div>
						</div>

					</div>
				</div>
				
				<div className="parentOption">
					<span>Blush</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">BLUSH STYLE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "Blush") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={blush} onChange={ (e: any) => {  leftRightChange(e.target.value - blush , "Blush")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "Blush") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>
						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "Blush") }} value={blushO} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight long" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>BLUSH COLOR</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Blush Color")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("blushColor")} stroke="url(#paint0_linear_241_2883)" strokeWidth="4"/>
									<defs>
									<linearGradient id="paint0_linear_241_2883" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
									<stop stopColor="#56FCFC"/>
									<stop offset="1" stopColor="#4E66A8"/>
									</linearGradient>
									</defs>
								</svg>

							</div>
						</div>


					</div>
				</div>

				<div className="parentOption">
					<span>Lipstick</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">Lipstick Style</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "Lipstick") }} fontSize={20} color={"#56FCFC"} />
								<input type='number' className="optionNumber" style={{ width: '76px' }} value={lipstick} onChange={ (e: any) => {  leftRightChange(e.target.value - lipstick , "Lipstick")  }}  />
								<FontAwesomeIcon  onClick={() => { leftRightChange(1, "Lipstick") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>
						<div className="optionDiv bumpDown">
							<div className="optionText">Opacity</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={(e) => { opacityChange(e, "Lipstick") }} value={lipstickO} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="leftRight long" style={{ padding: "7px 21px" }}>
								<div className="optionText" style={{ margin: '0px' }}>LIPSTICK COLOR</div>

								<svg onClick={() => {props.togglePicker(395, 336, "Lipstick Color")}} style={{ filter: "drop-shadow(0px 0px 6px rgba(86, 252, 252, 0.8)) drop-shadow(0px 0px 12px rgba(86, 252, 252, 0.5))" }} width="40" height="44" viewBox="0 0 40 44" fill="none" xmlns="http://www.w3.org/2000/svg">
									<path d="M2.94744 12.1547L20 2.3094L37.0526 12.1547V31.8453L20 41.6906L2.94744 31.8453V12.1547Z" fill={GetColor("lipstickColor")} stroke="url(#paint0_linear_241_2883)" strokeWidth="4"/>
									<defs>
									<linearGradient id="paint0_linear_241_2883" x1="20" y1="0" x2="20" y2="44" gradientUnits="userSpaceOnUse">
									<stop stopColor="#56FCFC"/>
									<stop offset="1" stopColor="#4E66A8"/>
									</linearGradient>
									</defs>
								</svg>

							</div>
						</div>


					</div>
				</div>

			</section>
	);
}
  
export default Makeup;