import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-solid-svg-icons'
import {fetchNui} from "../../utils/fetchNui";
import {useNuiEvent} from "../../hooks/useNuiEvent";

interface props { 
	currentMenu: string
}

const Parents: React.FC<props> = (props) => {

	const saveHeadBlend = (Data) => {
		fetchNui('saveheadblend', Data)
	}

	const [parentFace1, setParentFace1] = useState(0)
	const [parentFace1Max] = useState(45)

	const parentFace1Change = (incrementDecrease: string) => {

		var value = parentFace1

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > parentFace1Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = parentFace1Max
			}
		}

		const Data = {
			shapeFirst: value,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setParentFace1(value)
		saveHeadBlend(Data)

	}

	const [parentFace2, setParentFace2] = useState(0)
	const [parentFace2Max] = useState(45)

	const parentFace2Change = (incrementDecrease: string) => {

		var value = parentFace2

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > parentFace2Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = parentFace2Max
			}
		}

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: value,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setParentFace2(value)
		saveHeadBlend(Data)

	}

	const [parentFace3, setParentFace3] = useState(0)
	const [parentFace3Max] = useState(45)

	const parentFace3Change = (incrementDecrease: string) => {

		var value = parentFace3

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > parentFace3Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = parentFace3Max
			}
		}

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: value,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setParentFace3(value)
		saveHeadBlend(Data)

	}

	const [skinFace1, setSkinFace1] = useState(0)
	const [skinFace1Max] = useState(45)

	const skinFace1Change = (incrementDecrease: string) => {

		var value = skinFace1

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > skinFace1Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = skinFace1Max
			}
		}

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: value,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setSkinFace1(value)
		saveHeadBlend(Data)

	}

	const [skinFace2, setSkinFace2] = useState(0)
	const [skinFace2Max] = useState(45)

	const skinFace2Change = (incrementDecrease: string) => {

		var value = skinFace2

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > skinFace2Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = skinFace2Max
			}
		}

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: value,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setSkinFace2(value)
		saveHeadBlend(Data)

	}

	const [skinFace3, setSkinFace3] = useState(0)
	const [skinFace3Max] = useState(45)

	const skinFace3Change = (incrementDecrease: string) => {

		var value = skinFace3

		if (incrementDecrease === "+") {
			value = value + 1;
			if (value > skinFace3Max) {
				value = 0
			}
		} else if (incrementDecrease === "-") {
			value = value - 1;
			if (value < -1) {
				value = skinFace3Max
			}
		}

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: value,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setSkinFace3(value)
		saveHeadBlend(Data)

	}

	const [faceMix, setFaceMix] = useState(0)
	
	const faceMixChange = (e: any) => {

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: e.target.value,
			skinMix: skinMix,
			thirdMix: thirdFaceMix
		}

		setFaceMix(e.target.value)
		saveHeadBlend(Data)

	}

	const [skinMix, setSkinMix] = useState(0)
	
	const skinMixChange = (e: any) => {

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: e.target.value,
			thirdMix: thirdFaceMix
		}

		setSkinMix(e.target.value)
		saveHeadBlend(Data)
		
	}

	const [thirdFaceMix, setThirdFaceMix] = useState(0)
	
	const thirdFaceMixChange = (e: any) => {

		const Data = {
			shapeFirst: parentFace1,
			shapeSecond: parentFace2,
			shapeThird: parentFace3,
			skinFirst: skinFace1,
			skinSecond: skinFace2,
			skinThird: skinFace3,
			shapeMix: faceMix,
			skinMix: skinMix,
			thirdMix: e.target.value
		}
		
		setThirdFaceMix(e.target.value)
		saveHeadBlend(Data)
		
	}

	useNuiEvent('barberData', (e: any) => {

		const headBlend = e.headBlend
		for (var headBlendType in headBlend) {
			
			const value = headBlend[headBlendType]

			switch(headBlendType) {
				case "shapeFirst": 
					setParentFace1(value)
					break
				case "shapeSecond": 
					setParentFace2(value)
					break
				case "shapeThird": 
					setParentFace3(value)
					break
				case "skinFirst": 
					setSkinFace1(value)
					break
				case "skinSecond": 
					setSkinFace2(value)
					break
				case "skinThird": 
					setSkinFace3(value)
					break
				case "shapeMix": 
					setFaceMix(value * 100)
					break
				case "skinMix": 
					setSkinMix(value * 100)
					break
				case "thirdMix": 
					setThirdFaceMix(value * 100)
					break
			}			
		}

	})
	
	return (
		<section className="menuOptions" style={{ display: props.currentMenu === "parents" ? '' : 'none' }} >
				<div className="parentOption">
					<span>Parent One</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">FACE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { parentFace1Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{parentFace1}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { parentFace1Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">SKIN</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { skinFace1Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{skinFace1}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { skinFace1Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Parent Two</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">FACE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { parentFace2Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{parentFace2}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { parentFace2Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">SKIN</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { skinFace2Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{skinFace2}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { skinFace2Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">FACE MIX</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" min="1" max="100" onChange={faceMixChange} value={faceMix} />
									</div>
									
								</div>
								
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">SKIN MIX</div>
							<div className="leftRightScroll">
								<div>
									<div className="backdrop">
										<input type="range" onChange={skinMixChange} value={skinMix} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>

				<div className="parentOption">
					<span>Parent Three</span>
					<div className="optionGroup">

						<div className="optionDiv">
							<div className="optionText">FACE</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { parentFace3Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{parentFace3}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { parentFace3Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">SKIN</div>
							<div className="leftRight">
								<FontAwesomeIcon icon={faChevronLeft} onClick={ () => { skinFace3Change("-") } } fontSize={20} color={"#56FCFC"} />
								<div style={{ paddingLeft: '76px' }} className="optionNumber">{skinFace3}</div>
								<FontAwesomeIcon style={{ paddingLeft: '76px' }} onClick={ () => { skinFace3Change("+") } } icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
							</div>
						</div>

						<div className="optionDiv">
							<div className="optionText">FACE MIX</div>
							<div className="leftRightScroll long">
								<div>
									<div className="backdrop long">
										<input type="range" style={{ width: '496px' }} onChange={thirdFaceMixChange} value={thirdFaceMix} />
									</div>
									
								</div>
								
							</div>
						</div>

					</div>
				</div>
				
			</section>
	
	);
}
  
export default Parents;