import React, {useState} from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-solid-svg-icons'
import {fetchNui} from "../../utils/fetchNui";
import {useNuiEvent} from "../../hooks/useNuiEvent";

interface props { 
	currentMenu: string
	currentParent: string
}

const Skins: React.FC<props> = (props) => {

	const [maleSkin, setMaleSkin] = useState(0)
	const [maleSkinsMax, setMaleSkinsMax] = useState(0)

	const [femaleSkin, setFemaleSkin] = useState(0)
	const [femaleSkinsMax, setFemaleSkinsMax] = useState(0)

	const [faceSkin, setFaceSkin] = useState(0)
	const [faceSkinMax, setFaceSkinMax] = useState(0)
	const [faceSkinTexture, setFaceSkinTexture] = useState(0)
	const [faceSkinTextureMax, setFaceSkinTextureMax] = useState(0)


	useNuiEvent('totalData', (e: any) => {

		const skinTotal = e.skinTotal

		setMaleSkinsMax(skinTotal[0])
		setFemaleSkinsMax(skinTotal[1])

		const drawTotal = e.drawTotal
    
		for (var drawTotalItem in drawTotal) { // Clothing Menu
		  const type = drawTotal[drawTotalItem][0]
		  const max = drawTotal[drawTotalItem][1]
	
		  switch(type) {
			case "face": 
				setFaceSkinMax(max)
				break
		  }
		}
		
		const textureTotal = e.textureTotal

		for (var i in textureTotal) {
			const type = i
			const max = textureTotal[i]

			switch(type) {
				case "face": 
					setFaceSkinTextureMax(max)
					break
			}

		}

	})

	useNuiEvent('clothingData', (e: any) => {

		const skin = e.skin
		switch(skin.name) {
			case "skin_male":
				setMaleSkin(skin.value)
				setFemaleSkin(0)
				break
			case "skin_female":
				setFemaleSkin(skin.value)
				setMaleSkin(0)
				break
		}


		const drawables = e.drawables
		for (var drawItem in drawables) {
			const type = drawables[drawItem][0]
			const currentvalue = drawables[drawItem][1]

			switch(type) {
				case "face": 
					setFaceSkin(currentvalue)
					break
		  	}
			
		}

		const drawtextures = e.drawtextures
			for (var drawtexturesItem in drawtextures) {
				const type = drawtextures[drawtexturesItem][0]
				const currentvalue = drawtextures[drawtexturesItem][1]

				switch(type) {
				case "face": 
					setFaceSkinTexture(currentvalue)
					break
		  		}

		}

	})

	const leftRightChange = (incrementDecrease: number, option: string) => {

		let value;

		switch(option) {

			case "maleSkin":

				value = maleSkin
				value = value + incrementDecrease;

				if (value > maleSkinsMax) { value = 0
				} else if (value < -1) { value = maleSkinsMax }

				fetchNui('setped', {
					name: "skin_male",
					value: value,
				}).then(() => {
					setMaleSkin(value)
					setFemaleSkin(0)
				})

				break

			case "femaleSkin":

				value = femaleSkin
				value = value + incrementDecrease;

				if (value > femaleSkinsMax) { value = 0
				} else if (value < -1) { value = femaleSkinsMax }

				fetchNui('setped', {
					name: "skin_female",
					value: value,
				}).then(() => {
					setFemaleSkin(value)
					setMaleSkin(0)
				})

				break

			case "faceSkin": 

				value = faceSkin
				value = faceSkin + incrementDecrease

				if (value > faceSkinMax) { value = 0
				} else if (value < -1) { value = faceSkinMax }

				fetchNui('updateclothes', {
					name: "face",
					value: value,
					texture: faceSkinTexture
				}).then(retData => {
					setFaceSkin(value)
					setFaceSkinTexture(0)
					setFaceSkinTextureMax(retData)
				})

				break

			case "faceSkinTexture": 

				value = faceSkinTexture
				value = value + incrementDecrease;

				if (value > faceSkinTextureMax) { value = 0
				} else if (value < -1) { value = faceSkinTextureMax }

				fetchNui('updateclothes', {
					name: "face",
					value: faceSkin,
					texture: value
				})

				setFaceSkinTexture(value)

				break

		}

	}
	
	return (
		<section className="menuOptions" style={{ display: props.currentMenu === "skins" && props.currentParent === 'clothing' ? '' : 'none' }} >

			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">MALE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "maleSkin") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={maleSkin} onChange={ (e: any) => {  leftRightChange(e.target.value - maleSkin , "maleSkin")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "maleSkin") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">FEMALE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "femaleSkin") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={femaleSkin} onChange={ (e: any) => {  leftRightChange(e.target.value - femaleSkin , "femaleSkin")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "femaleSkin") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>
			
			<div className="optionGroup">

				<div className="optionDiv">
					<div className="optionText">FACE SKIN</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "faceSkin") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={faceSkin} onChange={ (e: any) => {  leftRightChange(e.target.value - faceSkin , "faceSkin")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "faceSkin") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

				<div className="optionDiv">
					<div className="optionText">TEXTURE</div>
					<div className="leftRight">
						<FontAwesomeIcon icon={faChevronLeft} onClick={() => { leftRightChange(-1, "faceSkinTexture") }} fontSize={20} color={"#56FCFC"} />
						<input type='number' className="optionNumber" style={{ width: '76px' }} value={faceSkinTexture} onChange={ (e: any) => {  leftRightChange(e.target.value - faceSkinTexture , "faceSkinTexture")  }}  />
						<FontAwesomeIcon  onClick={() => { leftRightChange(1, "faceSkinTexture") }} icon={faChevronRight} fontSize={20} color={"#56FCFC"} />
					</div>
				</div>

			</div>

			</section>
	
	);
}
  
export default Skins;